@import "./scss/config/widget.scss";
.feeInfoBlock {
  color: $notice-color;
}

.feeRow {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  &:not(:last-child) {
    margin-bottom: 0.5em;
  }
}

.feeRowTitle {
  font-size: 1.05em;
  white-space: nowrap;
}

.feeRowInfo {
  display: flex;
  align-items: flex-end;
}

.paleLoader {
  display: inline-block;
  opacity: 0.7;
}

.fee {
  font-family: 'Roboto Mono', monospace;
}

.serviceFeeConditions {
  margin: 2px 0;
  padding: 4px 7px;
  background-color: #88888818;
  font-size: 11px;
  border-left: 6px solid #8886;
  border-right: 2px solid #8886;
  border-radius: 4px;
}